.todo-tasks{
    padding: 0 40px 0 40px;
}
.task-div{
    margin-top: 20px;
    position: relative;
    align-items: center;
}
.task-div hr{
    margin-top: 10px;
}
.task-div-title{
    font-size: 25px;
    margin-left: 10px;
}
.task-div-checkbox {
    width: 20px;
    height: 20px;
  background-color: white;
  border-radius: 50%;
  border: 1px solid #ddd;
  appearance: none;
  -webkit-appearance: none;
  outline: none;
  cursor: pointer;
}

.task-div-checkbox:checked {
  background-color: #306fcd;
  border-color: #306fcd;
  box-shadow: inset 0 0 0px 5px white;
}
.task-delete-btn{
    position: absolute;
    background: transparent;
    border: none;
    cursor: pointer;
    right: 20px;
    padding-top: 5px;
    
}